import React, { Component } from 'react';
import { LazyLoadImage } from "react-lazy-load-image-component";
import "react-lazy-load-image-component/src/effects/opacity.css";
import Alternator from "../img/autoparts/Alternator.png";
import Battery from "../img/autoparts/Battery.png";
import Brakes from "../img/autoparts/Brakes.png";
import Catalytic from "../img/autoparts/Catalytic-converter.png";
import Engine from "../img/autoparts/Engine.png";
import FrontAxle from "../img/autoparts/Front-axle.png";
import FuelTank from "../img/autoparts/Fuel-tank.png";
import Muffler from "../img/autoparts/Muffler.png";
import Radiator from "../img/autoparts/Radiator.png";
import RearAxle from "../img/autoparts/Rear-axle.png";
import TailPipe from "../img/autoparts/Tailpipe.png";
import Transmission from "../img/autoparts/Transmission.png";


export default class AutoPartsCategory extends Component {
  render() {
    return (
      <div>
        <LazyLoadImage
            alt="auto spare parts dubai"
            effect="opacity"
            className="img-fluid AutoPartbrandImg shadow-sm"
            src={Alternator}
          />
          <LazyLoadImage
            alt="auto spare parts dubai"
            effect="opacity"
            className="img-fluid AutoPartbrandImg shadow-sm"
            src={Battery}
          />
          <LazyLoadImage
            alt="auto spare parts dubai"
            effect="opacity"
            className="img-fluid AutoPartbrandImg shadow-sm"
            src={Brakes}
          />
          <LazyLoadImage
            alt="auto spare parts dubai"
            effect="opacity"
            className="img-fluid AutoPartbrandImg shadow-sm"
            src={Catalytic}
          />
          <LazyLoadImage
            alt="auto spare parts dubai"
            effect="opacity"
            className="img-fluid AutoPartbrandImg shadow-sm"
            src={Engine}
          />
          <LazyLoadImage
            alt="auto spare parts dubai"
            effect="opacity"
            className="img-fluid AutoPartbrandImg shadow-sm"
            src={FrontAxle}
          />
          <LazyLoadImage
            alt="auto spare parts dubai"
            effect="opacity"
            className="img-fluid AutoPartbrandImg shadow-sm"
            src={FuelTank}
          />
          <LazyLoadImage
            alt="auto spare parts dubai"
            effect="opacity"
            className="img-fluid AutoPartbrandImg shadow-sm"
            src={Muffler}
          />
          <LazyLoadImage
            alt="auto spare parts dubai"
            effect="opacity"
            className="img-fluid AutoPartbrandImg shadow-sm"
            src={Radiator}
          />
          <LazyLoadImage
            alt="auto spare parts dubai"
            effect="opacity"
            className="img-fluid AutoPartbrandImg shadow-sm"
            src={RearAxle}
          />
          <LazyLoadImage
            alt="auto spare parts dubai"
            effect="opacity"
            className="img-fluid AutoPartbrandImg shadow-sm"
            src={TailPipe}
          />
          <LazyLoadImage
            alt="auto spare parts dubai"
            effect="opacity"
            className="img-fluid AutoPartbrandImg shadow-sm"
            src={Transmission}
          />

      </div>
    )
  }
}
